* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
	background-image: linear-gradient(45deg, #36d1dc 0%, #5b86e5 100%);
	color: #fff;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.header > h1 {
	font-weight: 900;
	padding: 2em 0;
	text-align: center;
}

.input-container > .input-field {
	padding: 12px;
	border-radius: 20px 0 0 20px;
	border: none;
	background: #fff;
	color: #000;
	opacity: 0.5;
	font-size: 20px;
}

.input-field:focus {
	outline: none;
}
.input-button:focus {
	outline: none;
}

.input-container > .input-button {
	padding: 12px;
	border-radius: 0 20px 20px 0;
	border: none;
	background: linear-gradient(45deg, #f12711 0%, #f5af19 100%);
	color: #fff;
	font-weight: bold;
	font-size: 20px;
}
